import React from "react"

import Layout from "../../components/Layout"

import { Link } from "gatsby"

import platform from "../../images/origens/platform.png";
import map from "../../images/origens/map.png";

export default function Home() {
  return (
    <Layout breadcrumbs={["Origens Brasil"]}>
      <div id="heading" >
        <h1>Origens Brasil<span className="registered">®</span></h1>
      </div>

      {/* Main */}
      <section id="main" className="wrapper">
        <div className="inner">

          <div className="content page">

            {<h5><Link to="/">Inicio</Link> &gt;&gt; Projetos  &gt;&gt; Origens Brasil<span className="registered">®</span></h5>}

            <header>
              <h2>Garantia de negócios pela floresta em pé</h2>
            </header>
            <p className="headline">As atividades econômicas que provêm sustento para as pessoas não precisam levar à destruição do meio-ambiente!</p>
            <p><span className="image right"><img src={map} alt="" /></span> O Origens Brasil<span className="registered">®</span> é um projeto, administrado pela ONG de certificação e manejo florestal “Imaflora”, que promove negócios sustentáveis na Amazônia em áreas prioritárias de conservação. Atuando junto a populações tradicionais e povos indígenas, o Origens Brasil<span className="registered">®</span> luta para promover o comércio justo e manter a floresta em pé ao oferecer garantia de origem, transparência e rastreabilidade para cadeias produtivas sustentáveis.</p>
            <p><span className="image left"><img src={platform} alt="" /></span>Atualmente, o Engajados é membro da coordenação técnica de todas as plataformas do Origens Brasil<span className="registered">®</span>. Somos responsáveis, desde o segundo semestre de 2018, pela absorção da tecnologia legada e pelo desenvolvimento de novas plataformas, construídas em cima do nosso framework “Dorothy”. Toda a tecnologia está sendo refatorada, expandida e organizada em torno da facilidade de colaboração e engajamento de todos os membros da Rede Origens Brasil<span className="registered">®</span>.</p>
            <br />
            <ul>
              <li>Acesso ao projeto em: <a href="https://www.origensbrasil.org.br/" target="_blank">www.origensbrasil.org.br</a></li>
            </ul>

          </div>
        </div>
      </section>
    </Layout>
  )
}
